import { CheckSolidIcon } from "@rp/react/web/icons"
import { Typography } from "@rp/react/web/mui/core"
import { useTheme } from "@rp/react/web/mui/core"

type Validation = {
  properLength: boolean
  containsNumbers: boolean
  containsUpperLower: boolean
  containsSpecial: boolean
  matchPasswords: boolean
}
type Props = {
  validation: Validation
  hint: string
  validationType: keyof Validation
}

export const LoginHintText = ({ validation, hint, validationType }: Props) => {
  const theme = useTheme()

  const color = validation[validationType] ? theme.palette.green[500] : theme.palette.grey[300]

  return (
    <Typography variant={"subtitle2"} color={theme.palette.grey[300]}>
      <CheckSolidIcon color={color} width={10} style={{ marginRight: "5px" }} />
      {hint}
    </Typography>
  )
}
